<template>
  <div class="pageView">
    <page-header @btn-action="openApp" />
    <div class="mainView">
      <div class="topContent">
        <span class="invitText">{{inviteInfo.ownerName}}{{inviteInfo.formatPhone}}</span>
        <span class="invitRoom">{{inviteInfo.roomAllName}}</span>
        <span class="invitText"></span>
      </div>
      <div class="bottomContent">
        <span class="invitText"></span>
        <div class="verifyCode">
          <diy-input ref="msgInput"></diy-input>
          <div
            class="sendBtn"
            @click="sendMsgCode"
            :class="{countDown : verifyCodeStr != '' &&  verifyCodeStr != ''}"
          >{{verifyCodeStr}}</div>
        </div>
        <div class="nextStepBtn" @click="registAndBind"></div>
      </div>
      <div class="remark">
        <div class="custom-checkbox" @click="isAgreeprotocol = !isAgreeprotocol">
          <img
            :src="isAgreeprotocol ? require('../../assets/images/checkbox_select.png') : require('../../assets/images/checkbox_unselect.png')"
          />
        </div>
        <span style="margin-left:5px;color:#BDBDBD"></span>
        <span style="color:#FB5768;">
          
          <span style="text-decoration: underline" @click="clickedProtocol(1)"></span>
          <span style="text-decoration: underline" @click="clickedProtocol(2)"></span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import DiyInput from "../../common/component/verifyInput";
import PageHeader from "../../components/WechatShare/PageHeader";
import {
  getPhoneValitService,
  addRoomMemberByPhone,
  inviteBindRoom
} from "../../service/shareService";
export default {
  name: "invitationRegist",
  components: {
    PageHeader,
    DiyInput
  },
  data() {
    return {
      isAgreeprotocol: false,
      phoneDatas: [],
      verifyCodeStr: "",
      phoneNum: "",
      inviteInfo: {}
    };
  },
  computed: {
    messageCode() {
      return this.$refs.msgInput.code;
    }
  },
  mounted() {
    this.inviteInfo = JSON.parse(this.$route.query.data);
    this.phoneNum = this.inviteInfo.phone;
    this.androidShareOpenApp("myfamily?type=1"); //android APP
  },
  methods: {
    sendMsgCode() {
      if (
        this.verifyCodeStr != "" &&
        this.verifyCodeStr != ""
      ) {
        return;
      }
      let reqData = {
        PHONE: this.phoneNum
      };
      getPhoneValitService(reqData)
        .then(res => {
          if (res.success == false) {
            //
            this.$toast(res.msg);
          } else {
            //
            this.$toast("");
            let totalTime = 60;
            this.verifyCodeStr = totalTime + "";
            let clock = window.setInterval(() => {
              totalTime--;
              if (totalTime > 0) {
                this.verifyCodeStr = totalTime + "";
              } else {
                window.clearInterval(clock);
                //
                this.verifyCodeStr = "";
              }
            }, 1000);
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    registAndBind() {
      if (this.messageCode.length == 0) {
        this.$toast("");
        return;
      }
      if (this.messageCode.length != 6) {
        this.$toast("");
        return;
      }
      if (!this.isAgreeprotocol) {
        this.$toast("");
        return;
      }
      let param = {
        message: this.messageCode,
        phone: this.inviteInfo.phone,
        roomAllName: this.inviteInfo.roomAllName,
        roomCode: this.inviteInfo.roomCode,
        userCode: this.inviteInfo.ownerCode
      };
      inviteBindRoom(param)
        .then(res => {
          if (res.resultCode == "0" || res.resultCode == "10023") {
            this.$router.push({
              name: "invitationResult",
              query: {
                resultCode: res.resultCode,
                roomName: this.inviteInfo.roomAllName
              }
            });
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    openApp() {
      let urlString = "myfamily?type=1";
      this.shareOpenApp(urlString);
    },
    //
    clickedProtocol: function(index) {
      //1 2
      let type = index == 1 ? 1 : 3;
      this.$router.push({
        name: "protocolPage",
        query: { pageType: type }
      });
    }
  }
};
</script>
